@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,200&display=swap);
body {
  margin: 0;
  font-family: 'Poppins';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  background-color: #0080ff;
  padding: 10px;
}

header h1{
  color: white;
  max-width: 1200px;
  margin:0 auto;
}

h2{
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;

}

.content{
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding:20px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap:100px;
  gap:100px;
}
/* Basic Styles*/

.skeleton {
    background: #ddd;
    margin: 10px 0;
    border-radius: 4px;

}

.skeleton.text{
    width:100%;
    height: 12px;

}

.skeleton.title{
    width: 50%;
    height: 20px;
    margin-bottom: 15px;
}

.skeleton.avatar{
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.skeleton.thumbnail{
    width: 100px;
    height: 100px;
    border-radius: 4px;
}

.skeleton-wrapper{
    margin:20px auto;
    padding: 10px 15px;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
}

/*Skeleton profile*/
.skeleton-profile{
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap:30px;
    gap:30px;
    align-items: center;
}

/*themes*/
.skeleton-wrapper.light{
    background: #f2f2f2;

}

.skeleton-wrapper.dark{
    background: #444444;

}

.skeleton-wrapper.dark.skeleton{
    background:#777;
}

/*animation effect*/
.shimmer-wrapper{
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 100%;
    -webkit-animation: loading .75s infinite;
            animation: loading .75s infinite;
}

.shimmer{
    width: 50%;
    height: 100%;
    background: rgba(255,255,255,0.2);
    transform: skewX(-20deg);
    box-shadow: 0 0 30px 30px rgba(255,255,255,0.05);
}

.dark .shimmer{
    background: rgba(255,255,255,0.05);
}

@-webkit-keyframes loading{
    0% {transform: translateX(-150%);}
    50% {transform: translateX(-60%);}
    100% {transform: translateX(150%);}
}

@keyframes loading{
    0% {transform: translateX(-150%);}
    50% {transform: translateX(-60%);}
    100% {transform: translateX(150%);}
}
